import { Field, InputType } from "@nestjs/graphql";
import { Type } from "class-transformer";
import { IsNotEmpty, Max, Min, ValidateNested } from "class-validator";
import { PaymentPlanInput } from "./payment-plan.input";


@InputType()
export class PaymentGatewayPlanInput {

  @Field(() => Number)
  @IsNotEmpty()
  @Max(100)
  @Min(0)
  percentage: number;

  @Field(() => PaymentPlanInput)
  @ValidateNested()
  @Type(() => PaymentPlanInput)
  paymentPlan: PaymentPlanInput;

}
